define(['jquery', 'select2'], function($) {
    return {
        init: function(selector) {
            $(selector).select2({
                placeholder: '-- الرجاء اختيار شخص --',
                allowClear: true,
                matcher: function(params, data) {
                    if ($.trim(params.term) === '') {
                        return data;
                    }
                    if (typeof data.element === 'undefined') {
                       return null;
                    }


                    var search = $(data.element).text() + ' ' + $(data.element).data('text');
                    if (search.toLowerCase().includes(params.term.toLowerCase())) {
                        return data;
                    }
                    return null;
                }
            });
        }
    };
});
